/* --- SEARCHBAR --- */
/* Container de l'input flottant */
.floatingInput {
  position: relative;
  width: 220px;
  margin-top: 1.5em;
  font-family: 'Roboto Mono', 'Courier New', monospace;
}

/* Input normal */
.floatingInput input {
  width: 100%;
  padding: 12px 12px 12px 12px;
  font-size: 1rem;
  border-radius: 6px;
  border: 2px solid #000000;
  border-left: 8px solid #000000;
  outline: none;
  background-color: #ffffff;
  color: #000000;
}

/* Label flottant */
.floatingInput label {
  position: absolute;
  left: 12px;
  top: 12px;
  color: #555;
  font-size: 1rem;
  pointer-events: none;
  transition: 0.2s ease all;
  background-color: white;
  padding: 0 4px;
}

/* Animation du label au focus ou texte présent */
.floatingInput input:focus + label,
.floatingInput input:not(:placeholder-shown) + label,
.floatingInput input:valid + label {
  top: -8px;
  font-size: 0.8rem;
  color: #000;
}


.footerContainer__Portfolio:hover {
color: #022d5b;
}


/* ===================== DARK MODE ===================== */
.darkMode .floatingInput input {
  background-color: #1e1e1e;
  color: #ffffff;
  border: 2px solid #ffffff;
  border-left: 8px solid #ffffff;
}

.darkMode .floatingInput label {
  color: #aaa;
  background-color: #1e1e1e;
}

.darkMode .floatingInput input:focus + label,
.darkMode .floatingInput input:not(:placeholder-shown) + label,
.darkMode .floatingInput input:valid + label {
  color: #ffffff;
  background-color: #1e1e1e;
}



